@import "imports/bootstrap-rtl";
.owl-carousel{
  .slide-text{
    direction: rtl;
  }
}
.navbar{
  @media (min-width: 768px){
    .navbar-nav.navbar-right{
      float: left !important;
    }
  }
}
@media screen and (min-width: 767px){
  .navbar li.languages {
      left: 22px;
      right: auto;
  }
  .navbar ul.dropdown-menu {
    left: auto;
    right: 0;
  }
}
.title-bar{
  >*{
    padding: 0 0 0 10px;
  }
  .show-more{
    right: auto;
    left: 0;
    padding: 0 10px 0 0;
  }
}
.text-end{
  text-align: left;
}
.text-start{
  text-align: right;
}
footer{
  .lanquages{
    margin-right: 10px;
    margin-left: 0;
    a{
      margin: 0 15px 0 0;
    }
  }
}

.dropdown-menu{
  text-align: right;
}

.sub-header .breadcrumb>li+li:before{
  display: none;
}
.sub-header .breadcrumb>li+li:after{
  content: "\f104";
  padding: 0 5px;
  font-family: FontAwesome;
  font-size: 1.2em;
}